@import "~react-toolbox/lib/_colors"; 
 $theme-building: true;


/* Web Fonts - UI */
/* fallback */
@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: local('Material Icons'), local('MaterialIcons-Regular'), url(https://fonts.gstatic.com/s/materialicons/v16/2fcrYFNaTjcS6g4U3t-Y5ZjZjT5FdEJ140U2DJYC3mY.woff2) format('woff2');
}

:global(.material-icons) {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
}

/* Now we can start using the component */
.component {
  width: 400px !important;
  background-color: #f9f9f9;
  border-color: #c8c8c8;
  box-shadow: 0 0 18px #d9d9d9;
  position: absolute;
  right: 0px;
  overflow-x: visible;
  height: 1900px;
  padding: 0 0 200px 0;
  z-index:2000;
  direction: ltr;
}
.component .everything-worth-while {
  margin-bottom: 30px;
}
.container {
  width: 320px !important;
}

div[data-react-toolbox=snackbar] span {
  font-size: 14px;
}


